.clean-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

/* FLEX - BASE */
.flex {
  display: flex;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.column {
  flex-direction: column;
}

/* ALIGNMET/CONTENT */
.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.space-evenly {
  justify-content: space-evenly;
}

/* FLEX-GROW */
.flex-1 {
  flex-grow: 1;
}

.flex-2 {
  flex-grow: 2;
}

.flex-4 {
  flex-grow: 4;
}

.flex-8 {
  flex-grow: 8;
}

.main-layout {
  margin: 40px 20px 0;
}